export default class AppModel {
  constructor(state) {
    this.state = state;
    this.tickCount = 0;
    this.frameIndex = 0;
    this.canvas = '';
    this.ctx = '';
    this.color = 'white';
    this.savedImage = 0;
    this.mouseDownPos = 0;
  }

  static redrawMainCanvas(currentFrame) {
    const mainCanvas = document.getElementById('canvas');
    const mainCanvasCtx = mainCanvas.getContext('2d');
    mainCanvasCtx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);
    mainCanvasCtx.drawImage(currentFrame.getElementsByTagName('canvas')[0], 0, 0, mainCanvas.width, mainCanvas.height);
    mainCanvasCtx.imageSmoothingEnabled = false;
  }

  static changeActiveFrame(e) {
    const activeFrame = document.getElementsByClassName('active-frame')[0];
    activeFrame.classList.remove('active-frame');
    const framesContainer = document.getElementById('frames-container');
    let currentFrame = e.target;
    while (currentFrame !== framesContainer) {
      if (currentFrame.classList.contains('frame')) {
        currentFrame.classList.add('active-frame');
        AppModel.redrawMainCanvas(currentFrame);
      }
      currentFrame = currentFrame.parentNode;
    }
  }

  static deleteFrame(e) {
    let frameToDelete = e.target;
    const framesContainer = document.getElementById('frames-container');
    while (frameToDelete !== framesContainer && frameToDelete !== null) {
      if (frameToDelete.classList.contains('frame')) {
        if (frameToDelete.classList.contains('active-frame')) {
          if (frameToDelete.previousElementSibling) {
            frameToDelete.previousElementSibling.classList.add('active-frame');
            AppModel.redrawMainCanvas(frameToDelete.previousElementSibling);
            frameToDelete.parentNode.removeChild(frameToDelete);
          } else {
            frameToDelete.nextElementSibling.classList.add('active-frame');
            AppModel.redrawMainCanvas(frameToDelete.nextElementSibling);
            frameToDelete.parentNode.removeChild(frameToDelete);
          }
        } else {
          frameToDelete.parentNode.removeChild(frameToDelete);
        }
      }
      frameToDelete = frameToDelete.parentNode;
    }
  }

  static copyFrame(e, parentCtrl) {
    let copiedFrame = e.target;
    const size = parentCtrl.canvas.width;
    const framesContainer = document.getElementById('frames-container');
    while (copiedFrame !== framesContainer && copiedFrame !== null) {
      if (copiedFrame.classList.contains('frame')) {
        const frameCopy = copiedFrame.cloneNode(true);
        this.drawFrameCopy(frameCopy, copiedFrame);
        AppModel.redrawMainCanvas(frameCopy, size);
        copiedFrame.classList.remove('active-frame');
        copiedFrame.parentNode.insertBefore(frameCopy, copiedFrame.nextElementSibling);
      }
      copiedFrame = copiedFrame.parentNode;
    }
  }

  static getFrameCount() {
    const frames = document.querySelectorAll('div.frame');
    for (let i = 0; i < frames.length; i += 1) {
      const frame = document.getElementsByClassName('frame')[i];
      const frameNumBtn = frame.getElementsByClassName('frame-number-btn')[0];
      frameNumBtn.innerHTML = i + 1;
    }
  }

  static drawFrameCopy(frameCopy, copiedFrame) {
    const copiedFrameCanvas = copiedFrame.getElementsByTagName('canvas')[0];
    const frameCopyCanvas = frameCopy.getElementsByTagName('canvas')[0];
    const frameCopyCanvasCtx = frameCopyCanvas.getContext('2d');
    frameCopyCanvasCtx.drawImage(copiedFrameCanvas, 0, 0, 96, 96);
    frameCopyCanvasCtx.imageSmoothingEnabled = false;
  }

  static drawOnActiveFrame(mainCanvas) {
    const activeFrame = document.getElementsByClassName('active-frame')[0];
    const activeFrameCanvas = activeFrame.getElementsByTagName('canvas')[0];
    const frameCanvasCtx = activeFrameCanvas.getContext('2d');
    frameCanvasCtx.clearRect(0, 0, activeFrameCanvas.width, activeFrameCanvas.height);
    frameCanvasCtx.drawImage(mainCanvas, 0, 0, 96, 96);
    frameCanvasCtx.imageSmoothingEnabled = false;
  }

  static showCursorCoords(e) {
    const x = e.offsetX;
    const y = e.offsetY;
    const cursorCoords = document.getElementById('cursor-coords');
    cursorCoords.style.visibility = 'visible';
    cursorCoords.innerHTML = `Coords: ${x} : ${y}`;
  }

  static hideCursorCoords() {
    const cursorCoords = document.getElementById('cursor-coords');
    cursorCoords.style.visibility = 'hidden';
  }

  static getMouseDownPosition(e) {
    const mouseDownPos = { x: e.offsetX, y: e.offsetY };
    return mouseDownPos;
  }

  static saveCanvasImage(canvas, ctx) {
    const savedImage = ctx.getImageData(0, 0, canvas.width, canvas.height);
    return savedImage;
  }

  static startDrawing(e, parentCtrl) {
    this.ctx = parentCtrl.ctx;
    this.canvas = parentCtrl.canvas;
    this.ctx.beginPath();
    this.ctx.moveTo(e.offsetX, e.offsetY);
    const tool = document.getElementsByClassName('active-tool')[0];
    this.color = document.getElementsByClassName('active-color')[0].value;
    if (tool.id === 'bucket') {
      AppModel.fillCanvas();
      AppModel.drawOnActiveFrame(this.canvas);
    } else if (tool.id === 'color-picker') {
      AppModel.pickColor(e);
    }
  }

  static draw(e, parentCtrl) {
    if (parentCtrl.drawing === true) {
      const x = e.offsetX;
      const y = e.offsetY;
      this.canvas = parentCtrl.canvas;
      this.ctx = parentCtrl.ctx;
      this.savedImage = parentCtrl.savedCanvasImg;
      this.mouseDownPos = parentCtrl.mouseDownPos;
      const tool = document.getElementsByClassName('active-tool')[0];
      this.color = document.getElementsByClassName('active-color')[0].value;
      if (tool.id === 'pen') {
        AppModel.drawPen(x, y);
      } else if (tool.id === 'eraser') {
        AppModel.erase(x, y);
      } else if (tool.id === 'stroke') {
        AppModel.redrawCanvasImage();
        AppModel.drawLine(x, y);
      } else if (tool.id === 'circle') {
        AppModel.redrawCanvasImage();
        AppModel.drawCircle(x, y);
      } else if (tool.id === 'rectangle') {
        AppModel.redrawCanvasImage();
        AppModel.drawRectangle(x, y);
      } else if (tool.id === 'move') {
        AppModel.redrawCanvasImage();
        AppModel.moveCanvas(x, y);
      }
      AppModel.drawOnActiveFrame(this.canvas);
    }
  }

  static drawPen(x, y) {
    this.ctx.lineWidth = 1;
    this.ctx.lineTo(x, y);
    this.ctx.strokeStyle = this.color;
    this.ctx.stroke();
    this.ctx.imageSmoothingEnabled = false;
  }

  static erase(x, y) {
    this.ctx.clearRect(x, y, 2, 2);
  }

  static drawLine(x, y) {
    this.ctx.beginPath();
    this.ctx.moveTo(this.mouseDownPos.x, this.mouseDownPos.y);
    this.ctx.lineTo(x, y);
    this.ctx.strokeStyle = this.color;
    this.ctx.stroke();
  }

  static updateShape(x, y) {
    const shapeSize = {
      width: 0,
      height: 0,
      left: 0,
      top: 0,
    };
    shapeSize.width = Math.abs(x - this.mouseDownPos.x);
    shapeSize.height = Math.abs(y - this.mouseDownPos.y);
    if (x > this.mouseDownPos.x) {
      shapeSize.left = this.mouseDownPos.x;
    } else {
      shapeSize.left = x;
    }
    if (y > this.mouseDownPos.y) {
      shapeSize.top = this.mouseDownPos.y;
    } else {
      shapeSize.top = y;
    }
    return shapeSize;
  }

  static drawCircle(x, y) {
    const shape = AppModel.updateShape(x, y);
    const radius = shape.width;
    this.ctx.beginPath();
    this.ctx.arc(this.mouseDownPos.x, this.mouseDownPos.y, radius, 0, Math.PI * 2);
    this.ctx.strokeStyle = this.color;
    this.ctx.stroke();
  }

  static drawRectangle(x, y) {
    const shape = AppModel.updateShape(x, y);
    this.ctx.strokeRect(shape.left, shape.top, shape.width, shape.height);
  }

  static fillCanvas() {
    this.ctx.fillStyle = this.color;
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
  }

  static moveCanvas(x, y) {
    this.ctx.translate(x, y);
  }

  static pickColor(e) {
    const x = e.offsetX;
    const y = e.offsetY;
    const pixelData = this.ctx.getImageData(x, y, 1, 1);
    const pixelColor = AppModel.rgbToHex(pixelData.data[0], pixelData.data[1], pixelData.data[2]);
    document.getElementsByClassName('active-color')[0].value = pixelColor;
  }

  static colorToHex(color) {
    const hex = color.toString(16);
    return hex.length === 1 ? `0${hex}` : hex;
  }

  static rgbToHex(r, g, b) {
    return `#${AppModel.colorToHex(r)}${AppModel.colorToHex(g)}${AppModel.colorToHex(b)}`;
  }

  // static rotateCanvas() {

  // }

  static stopDrawing() {
    return false;
  }

  static redrawCanvasImage() {
    this.ctx.putImageData(this.savedImage, 0, 0);
  }

  static changeTool(e) {
    const { target } = e;
    let currentTool;
    const activeElement = document.getElementsByClassName('active-tool')[0];
    activeElement.classList.remove('active-tool');
    target.classList.add('active-tool');

    switch (target.id) {
      case 'pen':
        currentTool = 'pen';
        break;
      case 'stroke':
        currentTool = 'stroke';
        break;
      case 'eraser':
        currentTool = 'eraser';
        break;
      case 'bucket':
        currentTool = 'bucket';
        break;
      case 'circle':
        currentTool = 'circle';
        break;
      case 'rectangle':
        currentTool = 'rectangle';
        break;
      case 'hexagon':
        currentTool = 'hexagon';
        break;
      case 'move':
        currentTool = 'move';
        break;
      case 'color-picker':
        currentTool = 'color-picker';
        break;
      default:
        currentTool = 'pen';
    }
    return currentTool;
  }

  static changeToolByKeyPress(e) {
    let currentTool;
    const activeElement = document.getElementsByClassName('active-tool')[0];
    activeElement.classList.remove('active-tool');

    switch (e.code) {
      case 'KeyQ':
        currentTool = 'pen';
        break;
      case 'KeyW':
        currentTool = 'stroke';
        break;
      case 'KeyE':
        currentTool = 'eraser';
        break;
      case 'KeyR':
        currentTool = 'bucket';
        break;
      case 'KeyT':
        currentTool = 'circle';
        break;
      case 'KeyY':
        currentTool = 'rectangle';
        break;
      case 'KeyU':
        currentTool = 'hexagon';
        break;
      case 'KeyI':
        currentTool = 'move';
        break;
      case 'KeyO':
        currentTool = 'color-picker';
        break;
      default:
        currentTool = 'pen';
    }
    document.getElementById(currentTool).classList.add('active-tool');
    return currentTool;
  }

  static swapDraggedFrame(dragTarget, dropTarget) {
    const dropTargetCopy = dropTarget.cloneNode(true);
    dragTarget.parentNode.insertBefore(dropTargetCopy, dragTarget);
    AppModel.drawFrameCopy(dropTargetCopy, dropTarget);
    dropTarget.parentNode.insertBefore(dragTarget, dropTarget);
    dropTarget.parentNode.removeChild(dropTarget);
  }

  static dropFrame(e) {
    const draggedFrame = document.getElementById('dragged');
    if (draggedFrame) {
      const framesContainer = document.getElementById('frames-container');
      let { target } = e;
      target.style.cursor = 'pointer';
      while (target !== framesContainer && target !== null) {
        if (target.classList.contains('frame')) {
          draggedFrame.classList.remove('active-frame');
          AppModel.swapDraggedFrame(draggedFrame, target);
          draggedFrame.classList.remove('dragged-frame');
          document.getElementsByClassName('dragged-frame')[0].classList.remove('dragged-frame');
          draggedFrame.removeAttribute('id');
          draggedFrame.classList.add('active-frame');
        }
        target = target.parentNode;
      }
    }
  }

  static setFpsValue(fps) {
    const displayFps = document.querySelector('.fps');
    const fpsValue = fps.value;
    displayFps.innerHTML = `FPS: ${fpsValue}`;
  }

  static drawAnimationFrame() {
    const currentFrame = document.getElementsByClassName('frame')[this.frameIndex];
    const currentFrameCanvas = currentFrame.getElementsByTagName('canvas')[0];
    const previewCanvas = document.getElementById('preview-canvas');
    const previewCanvasCtx = previewCanvas.getContext('2d');
    previewCanvasCtx.clearRect(0, 0, previewCanvas.width, previewCanvas.height);
    previewCanvasCtx.drawImage(currentFrameCanvas, 0, 0, 200, 200);
    previewCanvasCtx.imageSmoothingEnabled = false;
  }

  static checkFps(fps, frames) {
    this.tickCount += 1;
    if (this.tickCount > (1000 / fps) / 16) {
      this.tickCount = 0;
      if (this.frameIndex < frames.length - 1) {
        this.frameIndex += 1;
      } else {
        this.frameIndex = 0;
      }
    }
  }

  static startAnimation() {
    const loop = () => {
      const fps = document.querySelector('input[type="range"]').value;
      const frames = document.querySelectorAll('div.frame');
      AppModel.checkFps(fps, frames);
      AppModel.drawAnimationFrame(this);
      requestAnimationFrame(loop);
    };
    window.requestAnimationFrame(loop);
  }

  static initAnimation() {
    this.frameIndex = 0;
    this.tickCount = 0;
    AppModel.startAnimation();
  }

  static openFullscreen() {
    const previewAnimation = document.getElementsByClassName('animation-player')[0];
    previewAnimation.requestFullscreen();
  }

  static saveImage() {
    const saveImageBtn = document.getElementById('save-image');
    saveImageBtn.download = 'mypainting.jpg';
    saveImageBtn.href = document.getElementById('canvas').toDataURL();
  }
}
