export default class AppView {
  static loadArtPage() {
    document.getElementById('landing-page_main').style.display = 'none';
    document.getElementById('art-page_main').style.display = 'flex';
  }

  static loadLandingPage() {
    document.getElementById('landing-page_main').style.display = 'flex';
    document.getElementById('art-page_main').style.display = 'none';
  }

  static changeFrameBtnVisibility() {
    const frames = document.querySelectorAll('div.frame');
    const deleteBtn = document.getElementsByClassName('delete-frame-btn')[0];
    if (frames.length === 1) {
      deleteBtn.style.visibility = 'hidden';
    } else {
      frames.forEach((frame) => {
        const deleteFrameBtn = frame.getElementsByClassName('delete-frame-btn')[0];
        deleteFrameBtn.style.visibility = 'visible';
      });
    }
  }

  static addNewFrame() {
    const frame = document.createElement('div');
    frame.classList.add('frame', 'active-frame');
    frame.setAttribute('draggable', 'true');
    const frameBackground = document.createElement('div');
    frameBackground.classList.add('canvas-background');
    const canvas = document.createElement('canvas');
    canvas.setAttribute('width', '96px');
    canvas.setAttribute('height', '96px');
    const deleteBtn = document.createElement('button');
    deleteBtn.classList.add('frame-btn', 'delete-frame-btn');
    const copyBtn = document.createElement('button');
    copyBtn.classList.add('frame-btn', 'copy-frame-btn');
    const countBtn = document.createElement('button');
    countBtn.classList.add('frame-number-btn');

    const framesContainer = document.getElementById('frames-container');
    framesContainer.appendChild(frame);
    frame.appendChild(frameBackground);
    frameBackground.append(canvas, deleteBtn, copyBtn, countBtn);
    AppView.changeFrameBtnVisibility();
  }

  static changeCanvasSize(scale, size, currentSizeBtn) {
    currentSizeBtn.classList.add('btn-active');
    const mainCanvas = document.getElementById('canvas');
    mainCanvas.style.transform = `scale(${scale}, ${scale})`;
    mainCanvas.setAttribute('width', `${size}`);
    mainCanvas.setAttribute('height', `${size}`);
  }

  static getCanvasSize(e) {
    const smallSizeCanvas = document.getElementById('small-canvas');
    const mediumSizeCanvas = document.getElementById('medium-canvas');
    const largeSizeCanvas = document.getElementById('large-canvas');
    const activeSize = document.getElementsByClassName('btn-active')[0];
    activeSize.classList.remove('btn-active');
    let scale;
    let size;
    if (e.target === smallSizeCanvas) {
      scale = 18.8;
      size = 32;
      AppView.changeCanvasSize(scale, size, smallSizeCanvas);
    } else if (e.target === mediumSizeCanvas) {
      scale = 9.39;
      size = 64;
      AppView.changeCanvasSize(scale, size, mediumSizeCanvas);
    } else if (e.target === largeSizeCanvas) {
      scale = 4.69;
      size = 128;
      AppView.changeCanvasSize(scale, size, largeSizeCanvas);
    }
  }

  static openKeyboardShortcutsModal() {
    const modal = document.getElementById('keyboard-shortcut');
    modal.style.display = 'block';
  }

  static closeKeyboardShortcutsModal() {
    const modal = document.getElementById('keyboard-shortcut');
    modal.style.display = 'none';
  }

  static swapColors() {
    const primaryColor = document.getElementById('primary-color').value;
    const secondaryColor = document.getElementById('secondary-color').value;
    const tempColor = primaryColor;
    document.getElementById('primary-color').value = secondaryColor;
    document.getElementById('secondary-color').value = tempColor;
  }
}
