import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.tool = 'pen';
    this.canvas = document.getElementById('canvas');
    this.ctx = this.canvas.getContext('2d');
    this.fps = document.getElementById('fps-input');
    this.fullscreenBtn = document.getElementById('fullscreen-btn');
    this.drawing = false;
    this.canvasSize = 32;
    this.mouseDownPos = 0;
    this.savedCanvasImg = 0;
  }

  start() {
    const self = this;
    this.listenCanvas();
    App.changeVisiblePages();
    App.listenAddFrameButton(self);
    App.listenFrameEvents(self);
    App.listenSizeChangeBtn();
    this.listenFullscreenBtn();
    App.listenToolChanges();
    this.listenFps();
    AppView.changeFrameBtnVisibility();
    AppModel.initAnimation();
    App.listenKeyPress();
    App.listenKeyboardModal();
    App.listenSwapColorsBtn();
    App.listenSaveImageBtn();
    App.listenCanvasRotationBtn();
  }

  listenCanvas() {
    const self = this;
    this.canvas.addEventListener('mousedown', (e) => {
      AppModel.startDrawing(e, self);
      this.mouseDownPos = AppModel.getMouseDownPosition(e);
      this.savedCanvasImg = AppModel.saveCanvasImage(this.canvas, this.ctx);
      this.drawing = true;
    });
    this.canvas.addEventListener('mouseup', (e) => {
      this.drawing = AppModel.stopDrawing(e, this.canvas);
    });
    this.canvas.addEventListener('mousemove', (e) => {
      AppModel.draw(e, self);
      AppModel.showCursorCoords(e);
    });
    this.canvas.addEventListener('mouseleave', (e) => {
      this.drawing = AppModel.stopDrawing(e, this.canvas);
    });
    this.canvas.addEventListener('mouseleave', AppModel.hideCursorCoords);
  }

  static changeVisiblePages() {
    const createSpriteBtn = document.getElementById('create-sprite-btn');
    createSpriteBtn.addEventListener('click', AppView.loadArtPage);
    const logo = document.getElementById('logo');
    logo.addEventListener('click', AppView.loadLandingPage);
  }

  static listenFrameEvents(self) {
    const framesContainer = document.getElementById('frames-container');
    const frames = document.querySelectorAll('div.frame');

    framesContainer.addEventListener('mousedown', (e) => {
      AppModel.changeActiveFrame(e);
    });

    framesContainer.addEventListener('click', (e) => {
      if (e.target.classList.contains('delete-frame-btn')) {
        AppModel.deleteFrame(e);
        AppView.changeFrameBtnVisibility();
        AppModel.getFrameCount(frames);
      } else if (e.target.classList.contains('copy-frame-btn')) {
        AppModel.copyFrame(e, self);
        AppView.changeFrameBtnVisibility();
      }
      AppModel.getFrameCount();
    });

    frames.forEach((frame) => {
      frame.addEventListener('mousedown', (e) => {
        AppModel.changeActiveFrame(e);
      });
      frame.addEventListener('drag', (e) => {
        e.target.id = 'dragged';
      }, false);
      frame.addEventListener('dragstart', (e) => {
        e.target.style.cursor = 'grabbing';
      }, false);
      frame.addEventListener('dragover', (e) => {
        e.preventDefault();
        e.target.style.cursor = 'grabbing';
      }, false);
      frame.addEventListener('dragenter', (e) => {
        if (e.target.classList.contains('frame')) {
          e.target.classList.add('dragged-frame');
        }
      }, false);
    });
    framesContainer.addEventListener('drop', (e) => {
      e.preventDefault();
      AppModel.dropFrame(e);
      AppModel.getFrameCount();
    });
  }

  static listenAddFrameButton() {
    const addFrameBtn = document.getElementById('add-frame-btn');
    const canvas = document.getElementById('canvas');
    const ctx = canvas.getContext('2d');
    addFrameBtn.addEventListener('click', () => {
      const activeElement = document.getElementsByClassName('active-frame')[0];
      activeElement.classList.remove('active-frame');
      AppView.addNewFrame();
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      AppModel.getFrameCount();
      AppModel.startAnimation();
    });
  }

  static listenSizeChangeBtn() {
    const canvasSizeBtns = document.getElementById('canvas-size');
    canvasSizeBtns.addEventListener('mousedown', (e) => {
      this.canvasSize = AppView.getCanvasSize(e);
    });
  }

  listenFullscreenBtn() {
    this.fullscreenBtn.addEventListener('click', () => {
      AppModel.openFullscreen();
    });
  }

  listenFps() {
    this.fps.addEventListener('input', () => {
      AppModel.setFpsValue(this.fps);
    });
  }

  static listenToolChanges() {
    const tools = document.getElementById('tools-container');
    tools.addEventListener('click', (e) => {
      this.tool = AppModel.changeTool(e);
    });
  }

  static listenKeyboardModal() {
    const keyboardShortcutsBtn = document.getElementById('keyboard-icon');
    keyboardShortcutsBtn.addEventListener('click', () => {
      AppView.openKeyboardShortcutsModal();
    });
    const closeModalBtn = document.getElementsByClassName('close')[0];
    closeModalBtn.addEventListener('click', () => {
      AppView.closeKeyboardShortcutsModal();
    });
  }

  static listenKeyPress() {
    document.addEventListener('keypress', (e) => {
      AppModel.changeToolByKeyPress(e);
    });
  }

  static listenSwapColorsBtn() {
    const swapColorBtb = document.getElementById('swap-colors');
    swapColorBtb.addEventListener('click', (e) => {
      AppView.swapColors(e);
    });
  }

  static listenSaveImageBtn() {
    const saveImageBtn = document.getElementById('save-image');
    saveImageBtn.addEventListener('click', () => {
      AppModel.saveImage(saveImageBtn, this.canvas);
    });
  }

  static listenCanvasRotationBtn() {
    const rotateBtn = document.getElementById('rotate');
    rotateBtn.addEventListener('click', () => {
      AppModel.rotateCanvas();
    });
  }
}
